import RouterCreator from '@src/utils/enhanceRouter';

import Login from "../views/Admin/Login.vue";
import Register from "../views/Admin/Register.vue";
import Member from "../views/Admin/Member.vue";
import Buyorder from "../views/Purchase/Buyorder.vue";


const routes = [
  {
    path: "/ucenter/login",
    name: "Login",
    component: Login,
    meta: {
      title: ''
    }
  },
  {
    path: "/ucenter/register",//
    name: "Register",
    component: Register,
    meta: {
      title: ''
    }
  },
  {
    path: "/ucenter/member",
    name: "Member",
    component: Member,
    meta: {
      title: ''
    }
  },
  {
    path: "/ucenter/buyorder",//
    name: "buyorder",
    component: Buyorder,
    meta: {
      title: ''
    }
  },
  {
    path: "/ucenter/quotedprice",//-
    name: "QuotedPrice",
    component: () => import('../views/Purchase/QuotedPrice.vue'),
    meta: {
      title: ''
    }
  },
 
];

const router = RouterCreator.create(routes);

export default router;
